@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #1f2527;
}

.PageHeader {
  width: 100vw;
  overflow: hidden;
}

.Logo {
  font-size: 1.64em;
  font-family: 'Anton';
  color: #cdc;
  float: left;
}

  .PageHeader > .Logo {
    margin-left: 5vw;
  }

.SiteMenu {
  float: right;
  margin-right: 5vw;
}

  .SiteMenu > .SiteMenuItem {
    display: inline-block;
    float: left;
  }

    .SiteMenu > .SiteMenuItem > a {
      display: inline-block;
      text-decoration: none;
      padding: .5em 1em;
      color: #cdc;
    }

    .SiteMenu > .SiteMenuItem > a:hover {
      background-color: #9a9;
      color: #1f2527;
    }

    .SiteMenu > .SiteMenuItem.active > a,
    .SiteMenu > .SiteMenuItem.active > a:active,
    .SiteMenu > .SiteMenuItem.active > a:visited {
      color: #1f2527;
      background-color: #cdc;
    }

.PageHeaderTitle{
  font-family: 'Anton';
  color: yellow;
  font-size: 4em;
  margin: 15vh 5vw 0 5vw;
  clear: both;
}

.MovieList {
  font-family: Arial, sans-serif;
  font-size: 16px;
  color: orange;
  margin-left: 5vw;
  margin-right: 5vw;
  overflow-y: hidden;
  white-space: nowrap;

  /* Hide scrollbar for IE, Edge and Firefox */
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.MovieList::-webkit-scrollbar {
  display: none;
}

  .MovieList > .movie {
    display: inline-block;
    box-sizing: border-box;
    margin-right: 2.5vw;
    width: 15vw;
    height: 22.5vw;
    border-radius: .75vw;
    opacity: .64;
    cursor: pointer;
    transition: opacity .2s ease-in-out;
  }

  .MovieList > .movie:hover {
    opacity: 1;
  }

  .MovieList > .movie.liked {
    border: 5px solid yellow;
  }

  .MovieList > .loadMore {
    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    width: 15vw;
    height: 22.5vw;
    border-radius: .75vw;
    padding: 2vh 2vw;
    vertical-align: top;
    line-height: 18vw;
    font-weight: 900;
    letter-spacing: -1px;
    color: #1f2527;
    background-color: #cdc;
    cursor: pointer;
    opacity: .64;
    transition: opacity .2s ease-in-out;
  }

  .MovieList > .loadMore:hover {
    opacity: 1;
  }


@media screen and (max-width: 720px) {

  .MovieList > .movie {
    width: 30vw;
    height: 45vw;
    border-radius: 1.5vw;
  }

  .MovieList > .loadMore {
    width: 30vw;
    height: 45vw;
    border-radius: 1.5vw;
    line-height: 30vw;
    font-size: .82em;
  }

  .PageHeaderTitle{
    font-size: 2.5em;
  }
}
